body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

@-webkit-keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

.mb2 { margin-bottom: 1rem; }
.mr1 { margin-right: .5rem; }
.p3 { padding: 2rem; }
.container { margin: 0 auto; max-width: 620px; }
.block { display: block; }
.border { border: 1px solid #eee; }
.align-bottom { vertical-align: bottom; }



